<template >
    <div  v-if="pageNum>0">
        <div v-if="dataList.length<=pageNum" >
            <table-item  v-for="(item,index) in dataList" :key="index" :index="index" 
                :width-array="widthArray"     :slotMap ="slotMap"
                :step="step" :left="left" :keys="keys"
                :item ="item"  >
            </table-item>
        </div>
        
        <div class="table-page" v-else> 
            <table-item  v-if="pageList.length>0  &&update" v-for="(item,index) in pageList" :key="index" :index="item.tableIndex" 
                :width-array="widthArray"    :slotMap ="slotMap"
                :step="step" :left="left" :keys="keys"  
                :item ="item"  >
            </table-item>

            <el-pagination
            :current-page.sync="pageIndex"
            :page-size="pageNum"
            @current-change="init"
            layout="total,prev, pager, next"
            :total="dataList.length">
            </el-pagination>
        </div>
    </div>
</template>

<script>
import {Pagination} from 'element-ui'
import tableItem from './table-item.js'
import { mapGetters }   from 'vuex'
import Vue from 'vue'
Vue.use(Pagination)

export default {
    name:'tableBody',
    computed:{
        ...mapGetters(['pageNum']),
    },
    created(){
    },
    props:{
        slotMap:{
            type:Object,
            default:function(){
                return {}
            }
        },
        left:{
            type:Number,
            default:5
        },
        step:{
            required:true
        },
        pid:{
            required: true
        },
        id:{
            type:String,
            default:'id'
        },
        //id
        rootKey:{
            type:String,
            default:''
        }, 
        //
        parentKey:{
            type:String,
            default:'pid'
        },
        dataList:{
            type:Array,
            default:function(){
                return  []
            }
        },
        widthArray:{
            type:Array,
            default:function(){
                return  []
            }
        },
        keys:{
            type:Array,
            default:function(){
                return  []
            }
        },
    },
    components:{tableItem},
    data(){
        return{
            pageIndex:1,
            childList:[],
            pageList:[],
            update:true
        }
    },
    methods:{
        init(num){
            var page =this.pageNum;
            if(this.dataList.length>page){
                this.$nextTick(()=>{
                    var index =num==null?this.pageIndex:num;
                    
                    var arr =[];
                    var start =(index-1) * page;
                    for(var i =0;i<page;i++){
                        if((start+i)<this.dataList.length){
                            var  itmes =this.dataList[start+i];
                            itmes.tableIndex =i+start;
                            arr.push(itmes);
                        }
                    }
                    this.update =false;
                    this.$nextTick(()=>{
                        this.$set(this,'pageList',arr);
                        this.update =true;
                        this.$nextTick(()=>{
                            this.$store.commit('setPageChange',this.keys);
                        })
                    })
                });
                // console.log(' ',this.pageList)
            }
        },
        cloneObj(obj) {
            var newObj = {};
            if (obj instanceof Array) {
                newObj = [];
            }
            for (var key in obj) {
                var val = obj[key];
                newObj[key] = (val!==null && typeof val === 'object') ? this.cloneObj(val) : val;
            }
            return newObj;
        },
    },
    mounted(){
        this.init();
    }
}
</script>

